import axios from 'axios'
import moment from 'moment'
import { types } from '../types/types'

export const getAllProducts = () => {
  return (dispatch) => {
    axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/products`)
      .then((response) => {
        console.log(response)

        dispatch(getProducts(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addProductAPI = (product) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/products`, product)
      .then((response) => {
        dispatch(createProduct(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateProductAPI = (product, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/products/${id}`, product)
      .then((response) => {
        dispatch(updateProduct(product))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createProduct = (product) => ({
  type: types.createProduct,
  payload: product,
})

export const updateProduct = (product) => ({
  type: types.updateProduct,
  payload: product,
})

export const getProducts = (product) => ({
  type: types.getProducts,
  payload: product,
})
