import axios from 'axios'
import moment from 'moment'
import { types } from '../types/types'

export const getAllBranches = () => {
  return (dispatch) => {
    axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/branches`)
      .then((response) => {
        console.log(response)

        dispatch(getBranches(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addBranchAPI = (branch) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/branches`, branch)
      .then((response) => {
        dispatch(createBranch(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateBranchAPI = (branch, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/branches/${id}`, branch)
      .then((response) => {
        dispatch(updateBranch(branch))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createBranch = (branch) => ({
  type: types.createBranch,
  payload: branch,
})

export const updateBranch = (branch) => ({
  type: types.updateBranch,
  payload: branch,
})

export const getBranches = (branches) => ({
  type: types.getBranches,
  payload: branches,
})
