import React, { useEffect } from 'react'
import AddIcon from '@mui/icons-material/Add'
import { useForm as customForm, Form } from 'src/layout/useForm'
import { Grid, makeStyles } from '@material-ui/core'
import Controls from 'src/views/utils/controls/Controls'
import { Button, IconButton } from '@mui/material'
import { useSelector } from 'react-redux'
import SaleDetailTable from './SaleDetailTable'

const initialFValues = {
  quantity: 1,
  price: '',
  discount: '',
  employeeId: '',
  productId: '',
}
function subtotal(items) {
  return items
    .map((e) =>
      e.discount
        ? e.quantity * e.price - e.quantity * e.price * (e.discount / 100)
        : e.quantity * e.price,
    )
    .reduce((sum, i) => sum + i, 0)
}

export const SaleDetailForm = (props) => {
  const { setFormValues, formValues } = props
  const invoiceSubtotal = subtotal(formValues.sale_details)
  const invoiceTotal = invoiceSubtotal

  const validate = (fieldValues = values) => {
    let temp = { ...errors }
    if ('productId' in fieldValues)
      temp.productId = fieldValues.productId ? '' : 'Este campo es requerido'
    if ('quantity' in fieldValues)
      temp.quantity = fieldValues.quantity ? '' : 'Este campo es requerido'

    setErrors({
      ...temp,
    })
    if (fieldValues == values) return Object.values(temp).every((x) => x == '')
  }

  const { values, setValues, handleInputChange, resetForm, errors, setErrors } = customForm(
    initialFValues,
    true,
    validate,
  )
  const products = useSelector((state) => state.products)

  const handleCreateDetail = () => {
    if (validate()) {
      setFormValues({
        ...formValues,
        sale_details: [...formValues.sale_details, values],
      })
      setValues(initialFValues)
    }
  }

  useEffect(() => {
    if (values.productId) {
      setValues({
        ...values,
        price: products.find((e) => e.id == values.productId).sale_price,
      })
    }
  }, [values.productId])

  useEffect(() => {
    if (formValues.sale_details) {
      setFormValues({
        ...formValues,
        total: invoiceTotal,
        subtotal: invoiceTotal,
      })
    }
  }, [formValues.sale_details])

  return (
    <>
      <Grid item xs={12} sm={5}>
        <Controls.FormSelect
          size="small"
          name="productId"
          label="Producto"
          value={values.productId}
          onChange={(ev) => {
            handleInputChange(ev)
          }}
          items={products.filter((e) => e.status)}
          // items={products}
          error={errors.productId}
        />
      </Grid>
      <Grid item xs={4} sm={2}>
        <Controls.FormInput
          size="small"
          label="Cantidad"
          name="quantity"
          value={values.quantity}
          type="number"
          onChange={handleInputChange}
          error={errors.quantity}
        />
      </Grid>
      <Grid item xs={4} sm={2}>
        <Controls.FormInput
          size="small"
          label="Descuento"
          name="discount"
          value={values.discount}
          type="number"
          onChange={handleInputChange}
        />
      </Grid>
      <Grid item xs={4} sm={2}>
        <Controls.FormInput
          size="small"
          label="Precio"
          name="price"
          value={values.price}
          type="number"
          onChange={handleInputChange}
          disabled
        />
      </Grid>
      <Grid item xs={12} sm={1} style={{ alignSelf: 'center' }}>
        <IconButton
          onClick={handleCreateDetail}
          style={{
            width: '100%',
            height: '35px',
            borderRadius: 20,
            backgroundColor: 'rgb(54, 163, 234)',
            marginBottom: 5,
          }}
        >
          <AddIcon
            style={{
              // fontSize: '40px',
              color: 'white',
              // backgroundColor: '#36A3EA',
              // borderRadius: 20,
            }}
          />
          {/* hola */}
        </IconButton>
      </Grid>
    </>
  )
}
