import axios from 'axios'
import { types } from '../types/types'

export const getAllSales = () => {
  return (dispatch) => {
    axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/sales`)
      .then((response) => {
        console.log(response)

        dispatch(getSales(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addSaleAPI = (sale) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/sales`, sale)
      .then((response) => {
        dispatch(createSale(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateSaleAPI = (sale, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/sales/${id}`, sale)
      .then((response) => {
        dispatch(updateSale(sale))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const createSale = (sale) => ({
  type: types.createSale,
  payload: sale,
})

export const updateSale = (sale) => ({
  type: types.updateSale,
  payload: sale,
})

export const getSales = (sales) => ({
  type: types.getSales,
  payload: sales,
})
