import React, { useEffect, useMemo } from 'react'
import MaterialTable, { MTableAction, MTableBodyRow, MTableHeader } from 'material-table'

import Edit from '@material-ui/icons/Edit'
import SearchIcon from '@mui/icons-material/Search'
import CalendarTodayIcon from '@mui/icons-material/CalendarToday'
import PersonIcon from '@mui/icons-material/Person'
import { Spinner } from 'src/views/utils/Spinner'
import { PaperHeader } from 'src/views/utils/PaperHeader'
import { GridTitle } from 'src/views/utils/material-table/GridTitle'
import { GridAddButton } from 'src/views/utils/material-table/GridAddButton'
import { localizationConfig } from '../../utils/material-table/Localization'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment'
import { tableIcons } from 'src/views/utils/material-table/GridIcons'
import { headerStyle } from 'src/views/utils/material-table/GridHeaderStyle'
import { getAllSales, updateSaleAPI } from 'src/actions/sales'
import { getAllProducts } from 'src/actions/products'
import { getAllBranches } from 'src/actions/branches'
import { getAllCustomers } from 'src/actions/customers'
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core'
import SaleDetailTableGrid from './SaleDetailTableGrid'
import DescriptionIcon from '@mui/icons-material/Description'
import AttachMoneyIcon from '@mui/icons-material/AttachMoney'
import rowStyle from 'src/views/utils/material-table/GridRowStyle'
import LockOpenIcon from '@mui/icons-material/LockOpen'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'

const theme = createMuiTheme({
  overrides: {
    MuiTableSortLabel: {
      root: {
        '&:hover': {
          color: '#0047AD',
        },
      },
      active: {
        color: '#0047AD !important',
      },
      icon: {
        color: '#0047AD !important',
      },
    },

    // MuiTableRow: {
    //   root: {
    //     '&:hover': {
    //       backgroundColor: 'rgba(88, 183, 244, 0.3)',
    //     },
    //   },
    //   head: {
    //     '&:hover': {
    //       backgroundColor: 'white',
    //     },
    //   },
    //   footer: {
    //     '&:hover': {
    //       backgroundColor: 'white',
    //     },
    //   },
    // },
  },
})

// const theme = createMuiTheme({
//   overrides: {
//     MuiTableRowRoot: {
//       '&:hover': {
//         backgroundColor: 'rgba(88, 183, 244, 0.3)',
//       },
//     },

//     MuiTableRowHead: {
//       '&:hover': {
//         backgroundColor: 'white',
//       },
//     },

//     MuiTableRowFooter: {
//       '&:hover': {
//         backgroundColor: 'white',
//       },
//     },
//   },
// })

const SalesGrid = (props) => {
  const columnas = [
    {
      title: <GridTitle Icon={DescriptionIcon} title="Folio" />,
      field: 'folio',
    },
    {
      title: <GridTitle Icon={PersonIcon} title="Cliente" />,
      field: 'customer.name',
      customFilterAndSearch: (term, rowData) => {
        return `${rowData.customer.name} ${rowData.customer.lastname}`
          .toLowerCase()
          .includes(term.toLowerCase())
        // rowData.customer?.name?.toLowerCase().includes(term.toLowerCase())
      },
      render: (rowData) => `${rowData.customer.name} ${rowData.customer.lastname}`,
    },
    {
      title: <GridTitle Icon={AttachMoneyIcon} title="Subtotal" />,
      field: 'subtotal',
      type: 'currency',
    },
    {
      title: <GridTitle Icon={AttachMoneyIcon} title="Total" />,
      field: 'total',
      type: 'currency',
    },
    {
      title: <GridTitle Icon={CalendarTodayIcon} title="Fecha de venta" />,
      field: 'createdAt',
      type: 'date',
      defaultSort: 'desc',
      render: (rowData) => moment(rowData.createdAt).format('DD/MM/YYYY').toString(),
    },
  ]

  const { setOpenPopup, setRecordForEdit } = props

  const dispatch = useDispatch()
  const sales = useSelector((state) => state.sales)
  const branches = useSelector((state) => state.branches)
  const customers = useSelector((state) => state.customers)
  const products = useSelector((state) => state.products)
  useEffect(() => {
    products.length === 0 && dispatch(getAllProducts())
    branches.length === 0 && dispatch(getAllBranches())
    customers.length === 0 && dispatch(getAllCustomers())
    if (sales.length === 0) {
      dispatch(getAllSales())
    }
  }, [])

  return useMemo(
    () => (
      <>
        {sales != null ? (
          // <CCard>
          <>
            <PaperHeader title="Ventas" />
            <MuiThemeProvider theme={theme}>
              <MaterialTable
                localization={localizationConfig}
                icons={tableIcons}
                columns={columnas}
                data={sales}
                detailPanel={(rowData) => {
                  return <SaleDetailTableGrid values={rowData} />
                }}
                actions={[
                  {
                    icon: 'save',
                    tooltip: 'Save User',
                    onClick: (event, rowData) => setOpenPopup(true),
                    isFreeAction: true,
                  },
                  {
                    icon: () => <SearchIcon style={{ color: '#1380C7' }} />,
                    tooltip: 'Ver venta',
                    onClick: (event, rowData) => {
                      setOpenPopup(true)
                      setRecordForEdit(rowData)
                    },
                  },
                  (rowData) => ({
                    icon: () =>
                      rowData.status ? (
                        <LockOutlinedIcon style={{ color: '#1380C7' }} />
                      ) : (
                        <LockOpenIcon style={{ color: '#1380C7' }} />
                      ),
                    tooltip: rowData.status ? 'Cancelar Venta' : 'Reactivar Venta',
                    onClick: (event, rowData) => {
                      rowData.status = !rowData.status
                      dispatch(updateSaleAPI(rowData, rowData.id))
                    },
                  }),
                ]}
                components={{
                  Action: (props) => {
                    return (
                      <>
                        {props.action.icon === 'save' ? (
                          <GridAddButton
                            onClick={(event) => props.action.onClick(event, props.data)}
                          />
                        ) : (
                          <MTableAction {...props} />
                        )}
                      </>
                    )
                  },
                  Row: (props) => {
                    return (
                      <MTableBodyRow
                        {...props}
                        onDoubleClick={(e) => {
                          setOpenPopup(true)
                          setRecordForEdit(props.data)
                        }}
                      />
                    )
                  },
                }}
                options={{
                  search: true,
                  draggable: false,
                  showTitle: false,
                  thirdSortClick: false,
                  searchFieldAlignment: 'left',
                  actionsColumnIndex: -1,
                  searchFieldVariant: 'outlined',
                  searchFieldStyle: { margin: 15 },
                  headerStyle: headerStyle,
                  rowStyle: rowStyle,
                }}
              />
            </MuiThemeProvider>
          </>
        ) : (
          <Spinner />
        )}
      </>
    ),
    [sales],
  )
}

export default SalesGrid
