import React, { useEffect } from 'react'
import { DialogTitle, Dialog, DialogContent, Button, Typography } from '@mui/material'
import { makeStyles } from '@material-ui/core'
import CloseIcon from '@mui/icons-material/Close'

const useStyles = makeStyles((theme) => ({
  dialogWrapper: {
    // padding: theme.spacing(2),
    position: 'absolute',
    top: theme.spacing(5),
    maxHeight: '85vh',
  },
  dialogTittle: {
    backgroundColor: 'rgba(88,183,244,0.8)',
  },
}))

export const PopupForm = (props) => {
  const { title, children, openPopup, setOpenPopup, setRecordForEdit } = props

  const classes = useStyles()

  const handleClose = (event, reason) => {
    if ((reason && reason == 'backdropClick') || reason == 'escapeKeyDown') {
      setRecordForEdit(null)
      setOpenPopup(false)
    }
  }
  return (
    <Dialog
      open={openPopup}
      onClose={handleClose}
      maxWidth="md"
      classes={{ paper: classes.dialogWrapper }}
      disableEnforceFocus
      fullWidth={true}
    >
      <DialogTitle style={{ backgroundColor: '#36A3EA' }}>
        <div style={{ display: 'flex' }}>
          <Typography variant="h6" component="div" style={{ flexGrow: 1, color: 'white' }}>
            {title}
          </Typography>
          <Button
            style={{ fontSize: '15px', color: 'white' }}
            onClick={() => {
              setRecordForEdit(null)
              setOpenPopup(false)
            }}
          >
            <CloseIcon />
          </Button>
        </div>
      </DialogTitle>
      <DialogContent dividers>{children}</DialogContent>
    </Dialog>
  )
}
