import React, { useState, useEffect } from 'react'
import { Divider, Grid, Tooltip } from '@mui/material'
import { useForm as customForm, Form } from 'src/layout/useForm'
import Controls from 'src/views/utils/controls/Controls'
import { useSelector } from 'react-redux'
// import { FormRadioGroup } from 'src/views/utils/controls/FormRadioGroup'
// import InputMask from 'react-input-mask'
import { Autocomplete, IconButton, TextField } from '@mui/material'

import { SaleDetailForm } from './SaleDetailForm'
import SaleDetailTable from './SaleDetailTable'
import RestoreFromTrashIcon from '@mui/icons-material/RestoreFromTrash'
export const SaleForm = (props) => {
  const branches = useSelector((state) => state.branches)
  const customers = useSelector((state) => state.customers)
  const sales = useSelector((state) => state.sales)

  const { profile } = useSelector((state) => state.auth)

  const initialFValues = {
    id: 0,
    folio: `SV-${sales.length + 1}`,
    subtotal: '',
    createdAt: new Date(),
    total: '',
    userId: profile.id,
    branchId: profile.branchId,
    customerId: '',
    customer: null,
    sale_details: [],
  }

  const { recordForEdit, addOrEdit, cancelForm, setOpenAlert } = props
  const { values, setValues, handleInputChange, resetForm } = customForm(initialFValues)

  useEffect(() => {
    if (recordForEdit != null) {
      if (recordForEdit.id) {
        setValues({ ...recordForEdit })
      }
    }
  }, [recordForEdit])

  const handleSubmit = (e) => {
    e.preventDefault()
    values.sale_details.length === 0 ? setOpenAlert(true) : addOrEdit(values, resetForm)
  }

  const handleReset = () => {
    setValues({ ...values, sale_details: [] })
    // resetForm()
  }
  const handleCancel = () => {
    cancelForm(resetForm)
  }
  return (
    <Form
      onSubmit={handleSubmit}
      style={recordForEdit?.id ? { opacity: 0.8, pointerEvents: 'none' } : null}
    >
      <Grid container rowSpacing={0} columnSpacing={3} style={{ width: 'calc(100% + 24px)' }}>
        <Grid item xs={12} sm={4}>
          <h5 style={{ fontWeight: 'bold', textAlign: 'center' }}>
            Folio: <span style={{ fontSize: '15px', fontWeight: 'normal' }}>{values.folio}</span>
          </h5>
        </Grid>
        <Grid item xs={12} sm={4}>
          <h5 style={{ fontWeight: 'bold', textAlign: 'center' }}>
            Usuario: <span style={{ fontSize: '15px', fontWeight: 'normal' }}>{profile.name}</span>
          </h5>
        </Grid>
        <Grid item xs={12} sm={4}>
          <h5 style={{ fontWeight: 'bold', textAlign: 'center' }}>
            Fecha:{' '}
            <span style={{ fontSize: '15px', fontWeight: 'normal' }}>
              {new Date(values.createdAt).toLocaleDateString()}
            </span>
          </h5>
        </Grid>
        <Grid item xs={12}>
          <Divider
            light={false}
            orientation="horizontal"
            flexItem
            style={{ margin: 5, height: '4px', color: '#36A3EA', opacity: 1 }}
          ></Divider>
        </Grid>
        <Grid item xs={12}>
          <h5 style={{ marginTop: 5 }}>Datos Generales</h5>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Autocomplete
            name="customer"
            value={values.customer}
            isOptionEqualToValue={(option, value) => option.id === value.id}
            id="combo-box-demo"
            options={recordForEdit?.id ? customers : customers.filter((e) => e.status)}
            // options={customers}
            onChange={(e, value) =>
              handleInputChange({
                target: {
                  value: value,
                  name: 'customer',
                },
              })
            }
            getOptionLabel={(option) => `${option.name} ${option.lastname} - ${option.phone}`}
            renderInput={(params) => <TextField required {...params} label="Cliente" />}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Controls.FormSelect
            name="branchId"
            label="Sucursal"
            value={values.branchId || ''}
            onChange={handleInputChange}
            items={recordForEdit?.id ? branches : branches.filter((e) => e.status)}
            // items={branches}
            required
          />
        </Grid>
        <Grid item xs={6} sm={6}>
          <h5>Detalle Venta</h5>
        </Grid>
        <Grid item xs={6} sm={6} style={{ textAlign: 'right' }}>
          <Tooltip title="Limpiar Detalle">
            <IconButton
              style={{
                width: '35px',
                height: '35px',
                borderRadius: 20,
                backgroundColor: 'rgb(54, 163, 234)',
                color: 'white',
              }}
              onClick={handleReset}
            >
              <RestoreFromTrashIcon />
            </IconButton>
          </Tooltip>

          {/* <Controls.FormButton
            variant="contained"
            color="default"
            size="small"
            text="Limpiar"
            onClick={handleReset}
          /> */}
        </Grid>

        <SaleDetailForm setFormValues={setValues} formValues={values} />
        <Grid
          item
          xs={12}
          style={recordForEdit?.id ? { opacity: 0.9, pointerEvents: 'all' } : null}
        >
          <SaleDetailTable values={values} />
        </Grid>
        <Grid item xs={12} style={{ textAlign: 'right', paddingTop: 5 }}>
          <Controls.FormButton
            variant="text"
            color="default"
            size="large"
            text="Cancelar"
            onClick={handleCancel}
            style={{ pointerEvents: 'auto' }}
          />
          <Controls.FormButton
            variant="contained"
            color="primary"
            type="submit"
            size="large"
            text="Guardar"
          />
        </Grid>
      </Grid>
    </Form>
  )
}
