import { Paper, makeStyles } from '@material-ui/core'
import { Alert, Snackbar } from '@mui/material'
import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { addSaleAPI, updateSaleAPI } from 'src/actions/sales'
// import { addProductAPI, updateProductAPI } from 'src/actions/products'
import { PopupForm } from 'src/views/utils/controls/PopupForm'
import { SaleForm } from './SaleForm'
import SalesGrid from './SalesGrid'
// import ProductsGrid from './ProductsGrid'

const useStyles = makeStyles((theme) => ({
  pageContent: {
    // margin: theme.spacing(3),
    padding: theme.spacing(3),
  },
  searchInput: {
    width: '75%',
  },
}))

const Sales = () => {
  const dispatch = useDispatch()
  const [openPopup, setOpenPopup] = useState(false)
  const [openAlert, setOpenAlert] = useState(false)

  const [recordForEdit, setRecordForEdit] = useState(null)
  const classes = useStyles()

  const addOrEdit = (sale, resetForm) => {
    sale.customerId = sale.customer.id
    Object.keys(sale).forEach(function (key) {
      if (sale[key] === '') {
        sale[key] = null
      }
    })
    Object.keys(sale.sale_details).forEach(function (key) {
      Object.keys(sale.sale_details[key]).forEach(function (key2) {
        if (sale.sale_details[key][key2] === '') {
          key2 === 'discount'
            ? (sale.sale_details[key][key2] = 0)
            : (sale.sale_details[key][key2] = null)
        }
      })
    })

    if (sale.id === 0) {
      delete sale.id
      dispatch(addSaleAPI(sale))
    } else {
      delete sale.tableData
      dispatch(updateSaleAPI(sale, sale.id))
    }
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return
    }

    setOpenAlert(false)
  }

  const cancelForm = (resetForm) => {
    resetForm()
    setRecordForEdit(null)
    setOpenPopup(false)
  }
  return (
    <>
      <Paper>
        <SalesGrid setOpenPopup={setOpenPopup} setRecordForEdit={setRecordForEdit} />
        {/* <SaleForm recordForEdit={recordForEdit} addOrEdit={addOrEdit} /> */}
      </Paper>
      <PopupForm
        openPopup={openPopup}
        setOpenPopup={setOpenPopup}
        setRecordForEdit={setRecordForEdit}
        title="Venta"
      >
        <SaleForm
          recordForEdit={recordForEdit}
          addOrEdit={addOrEdit}
          setOpenAlert={setOpenAlert}
          cancelForm={cancelForm}
        />
      </PopupForm>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert
          onClose={handleClose}
          severity="warning"
          sx={{ width: '100%' }}
          style={{ backgroundColor: 'rgb(211, 47, 47)', color: 'white', fontWeight: 'bold' }}
        >
          Agregar al menos un producto !
        </Alert>
      </Snackbar>
    </>
  )
}
export default Sales
