import { Button } from '@mui/material'
import React from 'react'
import AddIcon from '@mui/icons-material/Add'
export const GridAddButton = (props) => {
  const { title, onClick } = props
  return (
    <Button
      variant="contained"
      onClick={onClick}
      style={{
        textTransform: 'none',
        margin: 1,
        backgroundColor: '#36A3EA',
        color: 'white',
        padding: 5,
        borderRadius: 15,
        width: 120,
        fontWeight: 'bold',
      }}
      size="large"
    >
      <AddIcon style={{ marginRight: 5 }} />
      Agregar
    </Button>
  )
}
