import { MenuItem } from '@material-ui/core'
import { FormControl, FormHelperText, InputLabel, Select } from '@mui/material'

import React from 'react'
import { SelectValidator } from 'react-material-ui-form-validator'

export const FormSelect = (props) => {
  const { name, label, value, onChange, items, required, size, error = null, ...other } = props

  return (
    <FormControl variant="outlined" required={required} size={size} {...(error && { error: true })}>
      <InputLabel>{label}</InputLabel>
      <Select label={label} name={name} value={value} onChange={onChange} {...other}>
        <MenuItem value="">Ninguno</MenuItem>
        {items.map((item, index) => (
          <MenuItem key={item.id} value={item.id}>
            {item.name}
          </MenuItem>
        ))}
      </Select>
      {error && <FormHelperText>{error}</FormHelperText>}
    </FormControl>
  )
}
