import axios from 'axios'
import moment from 'moment'
import { types } from '../types/types'

export const getAllCustomers = () => {
  return (dispatch) => {
    axios
      .get(`${process.env.REACT_APP_API_URL_BASE}/api/customers`)
      .then((response) => {
        console.log(response)

        dispatch(getCustomers(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const addCustomerAPI = (customer) => {
  return async (dispatch) => {
    await axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/customers`, customer)
      .then((response) => {
        dispatch(createCustomer(response.data))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const updateCustomerAPI = (customer, id) => {
  return async (dispatch) => {
    await axios
      .put(`${process.env.REACT_APP_API_URL_BASE}/api/customers/${id}`, customer)
      .then((response) => {
        dispatch(updateCustomer(customer))
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

