export const createCustomer = (customer) => ({
  type: types.createCustomer,
  payload: customer,
})

export const updateCustomer = (customer) => ({
  type: types.updateCustomer,
  payload: customer,
})

// export const changeStatusCustomer = (customer) => ({
//   type: types.changeStatusCustomer,
//   payload: customer,
// })

export const getCustomers = (customers) => ({
  type: types.getCustomers,
  payload: customers,
})
