import React from 'react'
import { TextValidator } from 'react-material-ui-form-validator'

export const FormInput = (props) => {
  const { name, label, value, error = null, onChange, ...other } = props
  return (
    <TextValidator
      {...other}
      variant="outlined"
      label={label}
      name={name}
      value={value}
      onChange={onChange}
      {...(error && { error: true, helperText: error })}
    />
  )
}
