import React, { useEffect } from 'react'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { useSelector } from 'react-redux'
import { Grid, withStyles } from '@material-ui/core'
import { Slide } from '@mui/material'

const TAX_RATE = 0.16

function ccyFormat(num) {
  return `$${num.toFixed(2)}`
}

function priceRow(quantity, price, discount) {
  return discount ? quantity * price - quantity * price * (discount / 100) : quantity * price
}

// function subtotal(items) {
//   return items
//     .map((e) =>
//       e.discount
//         ? e.quantity * e.price - e.quantity * e.price * (e.discount / 100)
//         : e.quantity * e.price,
//     )
//     .reduce((sum, i) => sum + i, 0)
// }

// const invoiceTaxes = TAX_RATE * invoiceSubtotal
// const invoiceTotal = invoiceTaxes + invoiceSubtotal

export default function SaleDetailTable(props) {
  const { values } = props
  const products = useSelector((state) => state.products)
  // const invoiceSubtotal = subtotal(values.sale_details)

  // const invoiceTotal = invoiceSubtotal

  const TableHeaderCell = withStyles((theme) => ({
    root: {
      fontWeight: 'bold',
    },
  }))(TableCell)
  const TableHeaderCell2 = withStyles((theme) => ({
    root: {
      fontWeight: 'bold',
      fontSize: '17px',
    },
  }))(TableCell)

  return values.sale_details.length !== 0 ? (
    <Paper sx={{ width: '100%', overflow: 'hidden' }}>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow style={{ backgroundColor: 'rgba(88,183,244,0.5)' }}>
              <TableHeaderCell2 align="center" colSpan={4}>
                Detalle
              </TableHeaderCell2>
              <TableHeaderCell2 align="right">Precio</TableHeaderCell2>
            </TableRow>
            <TableRow>
              <TableHeaderCell>Descripción</TableHeaderCell>
              <TableHeaderCell align="right">Cantidad</TableHeaderCell>
              <TableHeaderCell align="right">Precio Unitario</TableHeaderCell>
              <TableHeaderCell align="right">Descuento</TableHeaderCell>

              <TableHeaderCell align="right">Suma</TableHeaderCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {values.sale_details.map((row, index) => (
              <TableRow key={index} tabIndex={-1}>
                <TableCell>{products.find((e) => e.id == row.productId).name}</TableCell>
                <TableCell align="right">{row.quantity}</TableCell>
                <TableCell align="right">{ccyFormat(row.price)}</TableCell>
                <TableCell align="right">{row.discount ? `${row.discount}%` : `-`}</TableCell>

                <TableCell align="right">
                  {ccyFormat(priceRow(row.quantity, row.price, row.discount))}
                </TableCell>
              </TableRow>
            ))}

            <TableRow>
              <TableCell rowSpan={3} />
              <TableHeaderCell colSpan={2} align="right">
                Subtotal
              </TableHeaderCell>
              <TableCell colSpan={1} />
              <TableCell align="right">{ccyFormat(values.subtotal)}</TableCell>
            </TableRow>
            {/* <TableRow>
            <TableCell colSpan={1} />

            <TableCell align="right">IVA</TableCell>
            <TableCell align="right">{`${(TAX_RATE * 100).toFixed(0)} %`}</TableCell>
            <TableCell align="right">{ccyFormat(invoiceTaxes)}</TableCell>
          </TableRow> */}
            <TableRow>
              <TableHeaderCell colSpan={2} align="right">
                Total
              </TableHeaderCell>
              <TableCell colSpan={1} />
              <TableCell align="right">{ccyFormat(values.total)}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  ) : null
}
